import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { InterfaceHttpService } from '../../http/interface.http.servive';
import { PageMaintainBean } from '../../page-preserve/page-maintain/bean/page-maintain.bean';
import * as _ from 'lodash';
@Component({
    selector: 'page-configuration',
    templateUrl: './page-configuration.component.html',
    styleUrls: ['./page-configuration.component.scss'],
    providers: [InterfaceHttpService]
})
export class PageConfigurationComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input() inValue: any;
    msgs: Message[] = [];
    dataTable: any = [];

    //分页
    total: number;
    first: number = 0;
    ftpAttrList: any = [[]];//FTP利润属性列表
    fTPTimeOpt: any = [];
    pageGatherMaintainAdd: boolean = false;
    tableData: any;
    pageMaintainBean: PageMaintainBean = new PageMaintainBean()
    arr: any = [];
    ftpAttrListMember = {
        profit: '',
        profitAttr: '',
        profitParmFrom: '',
        profitParmTo: '',
    }
    fTPAttrOpt: any = [];
    ftproFit: any = [];
    checkValue:any = [];
    constructor(
        public interfaceHttpService: InterfaceHttpService
    ) {

    }

    ngOnInit() {
        this.pageMaintainBean.pageNum = 1;
        this.pageMaintainBean.pageSize = 10;
        this.query()
    }

    //查询
    query() {
        this.interfaceHttpService.queryPageControllerInfoById(this.inValue).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //保存
    save(){
        debugger
        this.outValue.emit(this.dataTable.pageList)
        // let param = {
        //     businessSenceId:this.inValue.businessSenceId,
        //     pageCollectId:this.inValue.pageCollectId,
        //     pageList:this.dataTable.pageList
        // }
        // this.interfaceHttpService.dualPageControllerInfo(param).subscribe(data => {
        //     if (data.returnCode.code == 'success') {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'success', summary: '提示', detail: '保存成功' })
        //         this.outValue.emit(false)
        //     } else {
        //         this.msgs = [];
        //         this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
        //     }
        // }, error => {
        //     this.msgs = [];
        //     this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        // })
    }

    //查询
    queryClick() {
        // this.pageMaintainBean['pageCollectId']=this.dataTable.pageCollectId;
        this.interfaceHttpService.queryPageListByPageCollectId({pageCollectId:this.inValue.pageCollectId}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.pageControllerList;
                // this.total = data.totalNum;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //分页
    paginate(event) {
        this.pageMaintainBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageMaintainBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();//调用查询的方法
    }

    select(event, item) {
        if (event == true) {
            this.arr.push(item)
        }
        if (event == false) {
            this.arr.forEach((element, index) => {
                if (element.pageId == item.pageId) {
                    this.arr.splice(index, 1)
                }
            });
        }
    }
    toDetail() {
        this.checkValue = []
        if(!this.dataTable.pageList){
            this.dataTable.pageList = []
        }
        this.arr = this.dataTable.pageList;
        if(this.dataTable.pageList&&this.dataTable.pageList.length>0){
            this.dataTable.pageList.forEach(element => {
                this.checkValue.push(element.pageId)
            });
        }
        this.pageGatherMaintainAdd = true;
        this.queryClick()
    }

    sure() {
        if (!this.dataTable.pageList) {
            this.dataTable.pageList = []
        }
        this.pageGatherMaintainAdd = false;
        this.dataTable.pageList = _.clone(this.arr)
    }

    //上
    up(car) {

        let carList = JSON.parse(JSON.stringify(this.dataTable.pageList));
        let i = this.getIndex(carList, car);
        if (i != -1 && i != 0) {
            let a = carList[i - 1];
            carList[i] = a;
            carList[i - 1] = car;
        }
        this.dataTable.pageList = carList;
    }

    //下
    down(car) {
        let carList = JSON.parse(JSON.stringify(this.dataTable.pageList));
        let i = this.getIndex(carList, car);
        if (i < carList.length - 1) {
            let a = carList[i + 1];
            carList[i] = a;
            carList[i + 1] = car;
        }
        this.dataTable.pageList = carList;
    }

    getIndex(list, item) {
        let index = -1;
        for (let i = 0; i < list.length; i++) {
            if (list[i].pageId == item.pageId) {
                index = i;
                break;
            }
        }
        return index
    }

    deleteTemplate(i) {
        this.dataTable.pageList.splice(i, 1);
        this.dataTable.pageList = _.clone(this.dataTable.pageList);
    }

    cancel(){
        this.outValue.emit(false)
    }
    reset(){
        this.dataTable.pageList = _.clone([])
    }

}
