import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { InterfaceHttpService } from '../../http/interface.http.servive';
@Component({
    selector: 'page-control-detail',
    templateUrl: './page-control-detail.component.html',
    styleUrls: ['./page-control-detail.component.scss'],
})
export class PageControlDetailComponent implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    msgs: Message[] = [];
    dataTable: any = [];
    constructor(
        public interfaceHttpService:InterfaceHttpService
    ) { }

    ngOnInit() {
        this.query()
    }

    query() {
        this.interfaceHttpService.queryPageControllerList({businessSenceId:this.inValue,pageNum:1,pageSize:10000}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.businessSenceList[0];;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    back(){
        this.outValue.emit(false)
    }


}
