import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { InterfaceHttpService } from '../../http/interface.http.servive';
import { PageGatherMaintainBean } from '../../page-data-set/page-gather-maintain/bean/PageGatherMaintain.bean';
import * as _ from 'lodash';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
    selector: 'page-control-add',
    templateUrl: './page-control-add.component.html',
    styleUrls: ['./page-control-add.component.scss'],
    providers: [InterfaceHttpService,Commfunc]
})
export class PageControlAddComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    msgs: Message[] = [];
    first: number = 0;
    dataTable: any = [];
    pageSize: number = 10;
    pageNum: number = 1;
    total: number;
    Header: string = "页面控制配置_页面集页面控制";
    configurationPage: boolean = false;
    businessSenceId:string;
    productiveAreaId:string;
    inValue:any;
    pageGatherMaintainAdd:boolean = false;
    pageGatherMaintainBean:PageGatherMaintainBean = new PageGatherMaintainBean()
    tableData:any;
    checkValue:any = [];
    arr:any=[];
    isPost:boolean = false;//岗位
    postName:string = '';
    postId:any;
    productiveArea:any = [];
    pageList:any=[];
    index:any;
    constructor(
        public interfaceHttpService:InterfaceHttpService,
        public commfunc:Commfunc
    ) { }

    ngOnInit() {
        this.pageGatherMaintainBean.pageNum = 1;
        this.pageGatherMaintainBean.pageSize = 10;
        this.productiveAreaId = '01';
        this.queryScence()
    }

    queryScence() {
        this.interfaceHttpService.querBusinessSenceList({ pageNum: 1, pageSize: 10000 }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productiveArea.push(...this.commfunc.toChangeTree(data.businessSenceList, 'businessSenceName', 'businessSenceId'))
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    query(){
        this.interfaceHttpService.queryPageControllerByCollectId({businessSenceId:this.productiveAreaId}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.pageControllerList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    toDetail(item,index) {
        this.configurationPage = true;
        this.index = index;
        this.inValue = {
            businessSenceId:this.productiveAreaId,
            pageCollectId:item.pageCollectId
        }
    }
    
    configurationCall(event) {
        this.configurationPage = false;
        this.pageList = event
    }

    GatherMain(){
        this.checkValue = []
        if(!this.dataTable){
            this.dataTable = []
        }
        this.arr = this.dataTable;
        if(this.dataTable&&this.dataTable.length>0){
            this.dataTable.forEach(element => {
                this.checkValue.push(element.pageCollectId)
            });
        }
        this.pageGatherMaintainAdd = true;
        this.queryClick()
    }

    //查询
    queryClick() {
        this.interfaceHttpService.queryPageCollectDefinitionList(this.pageGatherMaintainBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.pageCollectList;
                this.total = data.totalNum
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    save(){
        if((!this.dataTable)||(this.dataTable == '0')){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请配置页面集" })
        }
        this.dataTable[this.index]['pageList'] = this.pageList;
        debugger
        console.log(this.dataTable)
        let param = {
            businessSenceId:this.inValue.businessSenceId,
            positionList:this.postId,
            productList:[],
            pageControlList:this.dataTable
        }
        this.interfaceHttpService.dualPageControllerInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false)
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' })
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //删除
    deleteTemplate(i) {
        this.dataTable.splice(i, 1);
        this.dataTable = _.clone(this.dataTable);
    }

    //分页
    paginate(event) {
        this.pageGatherMaintainBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageGatherMaintainBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();//调用查询的方法
    }

    select(event,item) {
        if(event == true){
            this.arr.push(item)
        }
        if(event ==false) {
            this.arr.forEach((element,index) => {
                if(element.pageCollectId == item.pageCollectId){
                    this.arr.splice(index,1)
                }
            });
        }
    }

    sure() {
        if(!this.dataTable){
            this.dataTable = []
        }
        this.pageGatherMaintainAdd = false;
        this.dataTable = _.clone(this.arr)
    }

    reset(){
        this.dataTable = []
    }

    cancel(){
        this.outValue.emit(false)
    }

    //岗位
    queryPost(){
        this.isPost = true;
    }

    queryPostCall(event) {
        this.postName =''
        this.isPost = false;
        this.postId = [];
        event.forEach(element => {
            if(this.postName !=''){
                this.postName = this.postName +element.postName +',';
            } else{
                this.postName = element.postName +',';
            }
            
            this.postId.push(element.postId)
        });
    }
}
