import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { InterfaceHttpService } from '../../http/interface.http.servive';
import { PageGatherMaintainBean } from '../../page-data-set/page-gather-maintain/bean/PageGatherMaintain.bean';
import * as _ from 'lodash';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
    selector: 'page-control-update',
    templateUrl: './page-control-update.component.html',
    styleUrls: ['./page-control-update.component.scss'],
    providers:[Commfunc]
})
export class PageControlUpdateComponent implements OnInit {
    @Output() outValue = new EventEmitter();
    @Input('inValue') inValues: any;
    msgs: Message[] = [];
    first: number = 0;
    dataTable: any = [];
    pageSize: number = 10;
    pageNum: number = 1;
    total: number;
    Header: string = "页面控制配置_页面集页面控制";
    configurationPage: boolean = false;
    businessSenceId:string;
    productiveAreaId:string;
    inValue:any;
    pageGatherMaintainAdd:boolean = false;
    pageGatherMaintainBean:PageGatherMaintainBean = new PageGatherMaintainBean()
    tableData:any;
    arr:any=[];
    checkValue:any = [];
    isPost:boolean = false;//岗位
    postName:string = '';
    postId:any;
    productiveArea:any = [];
    productiveAreaName:string;
    index:string;
    pageList:any=[];
    constructor(
        public interfaceHttpService:InterfaceHttpService,
        public commfunc:Commfunc
    ) {
 
     }

    ngOnInit() {
        debugger
        this.pageGatherMaintainBean.pageNum = 1;
        this.pageGatherMaintainBean.pageSize = 10;
        this.productiveAreaId = this.inValues.businessSenceId;
        this.productiveAreaName = this.inValues.businessSenceName;
        this.query();
        
    }
    
    toDetail(item,index) {
        this.index = index;
        this.configurationPage = true;
        this.inValue = {
            businessSenceId:this.productiveAreaId,
            pageCollectId:item.pageCollectId
        }
    }
    
    configurationCall(event) {
        debugger
        this.configurationPage = false;
        this.pageList =_.clone(event)
        this.dataTable[this.index]['pageList'] = _.clone(this.pageList);
        this.dataTable[this.index]['pageControlList'] = _.clone(this.pageList);

    }

    GatherMain(){
        this.checkValue = []
        if(!this.dataTable){
            this.dataTable = []
        }
        this.arr = this.dataTable;
        if(this.dataTable&&this.dataTable.length>0){
            this.dataTable.forEach(element => {
                this.checkValue.push(element.pageCollectId)
            });
        }
        this.pageGatherMaintainAdd = true;
        this.queryClick()
    }

    //查询
    queryClick() {
        this.interfaceHttpService.queryPageCollectDefinitionList(this.pageGatherMaintainBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.pageCollectList;
                this.total = data.totalNum
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    save(){
        debugger
        if((!this.dataTable)||(this.dataTable == '0')){
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "请配置页面集" })
        }
       
        // if(this.pageList && this.pageList.length>0){
        //     this.dataTable[this.index]['pageList'] = this.pageList;
        // }
        this.dataTable.forEach((element,index) => {
            if(element.pageControlList.length=='1' && element.pageControlList[0].pageId==null){
                element.pageControlList=_.clone([])
            }
            this.dataTable[index]['pageList']=element.pageControlList
        });
        let param = {
            businessSenceId:this.inValues.businessSenceId,
            positionList:this.postId,
            productList:[],
            pageControlList:this.dataTable
        }
        this.interfaceHttpService.dualPageControllerInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.outValue.emit(false)
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' })
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }
     //删除
     deleteTemplate(i) {
        this.dataTable.splice(i, 1);
        this.dataTable = _.clone(this.dataTable);
    }

    //分页
    paginate(event) {
        this.pageGatherMaintainBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageGatherMaintainBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.queryClick();//调用查询的方法
    }

    query(){
        this.interfaceHttpService.queryPageControllerList({businessSenceId:this.productiveAreaId,pageNum:1,pageSize:10000}).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.businessSenceList[0].pageControllerList;
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }
    select(event,item) {
        if(event == true){
            this.arr.push(item)
        }
        if(event ==false) {
            this.arr.forEach((element,index) => {
                if(element.pageCollectId == item.pageCollectId){
                    this.arr.splice(index,1)
                }
            });
        }
    }

    sure() {
        if(!this.dataTable){
            this.dataTable = []
        }
        this.pageGatherMaintainAdd = false;
        this.dataTable = _.clone(this.arr)
    }

    reset(){
        this.dataTable = []
    }

    cancel(){
        this.outValue.emit(false)
    }

     //岗位
     queryPost(){
        this.isPost = true;
    }

    queryPostCall(event) {
        this.postName ='';
        this.isPost = false;
        this.postId = [];
        event.forEach(element => {
            if(this.postName !=''){
                this.postName = this.postName +element.postName +',';
            } else{
                this.postName = element.postName +',';
            }
            
            this.postId.push(element.postId)
        });
    }

}
