import { Component, OnInit } from '@angular/core';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Message } from 'primeng/primeng';
import { PageControlBean } from './bean/PageControl.bean';
import { InterfaceHttpService } from '../http/interface.http.servive';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
@Component({
    selector: 'page-control',
    templateUrl: './page-control.component.html',
    styleUrls: ['./page-control.component.scss'],
    providers: [Commfunc]
})
export class PageControlComponent implements OnInit {
    tabMesg: any = tableMessage;//表格中无数据时显示的内容
    msgs: Message[] = [];
    first: number = 0;
    dataTable: any;
    total: number;
    addHeader: string = "页面控制配置";
    addPage: boolean = false;
    updatePage: boolean = false;
    detailPage: boolean = false;
    updateValue: any;
    pageControlBean: PageControlBean = new PageControlBean()
    detailValue: any;
    productiveArea: any = [];
    constructor(
        public interfaceHttpService: InterfaceHttpService,
        public commfunc: Commfunc
    ) { }

    ngOnInit() {
        this.pageControlBean.pageNum = 1;
        this.pageControlBean.pageSize = 10;
        this.query();
        this.queryScence();
    }
    //查询
    queryScence() {
        this.interfaceHttpService.querBusinessSenceList({ pageNum: 1, pageSize: 10000 }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.productiveArea.push(...this.commfunc.toChangeTree(data.businessSenceList, 'businessSenceName', 'businessSenceId'))
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    query() {
        this.interfaceHttpService.queryPageControllerList(this.pageControlBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.dataTable = data.businessSenceList;
                this.total = data.totalNum
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message })
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' })
        })
    }

    //新增
    add() {
        this.addHeader = "新增";
        this.addPage = true;
    }

    //新增回掉
    addCall(event) {
        this.addPage = false;
        this.query()
    }

    //修改
    toUpdate(data) {
        this.updateValue = {businessSenceId:data.businessSenceId,businessSenceName:data.businessSenceName};
        this.updatePage = true;

    }

    updateCall() {
        this.updatePage = false;
    }

    toDetail(data) {
        this.detailPage = true;
        this.detailValue = data.businessSenceId;
    }

    detailCall() {
        this.detailPage = false;
    }

    //重置
    reset() {
        this.pageControlBean.businessSenceName = '';
    }

    //分页
    paginate(event) {
        this.pageControlBean.pageSize = event.rows;//每页显示的条数
        let currentPage = event.page + 1; //当前页
        this.pageControlBean.pageNum = currentPage;
        this.first = event.page * event.rows;
        this.query();//调用查询的方法
    }

}
